import * as React from "react";
import "./drawerPC.css";
import { Link } from "react-router-dom";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Button from "@mui/material/Button";

export default function TemporaryDrawer() {
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      width="100%"
      height="100%"
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
      backgroundColor="#10a172"
    >
      <div class="weaponDrawer_pc">
        <h2 class="weaponDrawer_title_pc">SORT</h2>
        <ul class="weaponDrawer_ul_pc">
          <li class="weaponDrawer_ul_title_pc">種類で分ける</li>
          <Link to="/weapon2">
            <li>-近接武器-</li>
          </Link>
          <Link to="/weapon3">
            <li>-遠距離武器-</li>
          </Link>
          <Link to="/weapon4">
            <li>-盾-</li>
          </Link>
        </ul>
        <ul class="weaponDrawer_ul_pc">
          <li class="weaponDrawer_ul_title_pc">特性で分ける</li>
          <Link to="/weapon5">
            <li>-暴虐-</li>
          </Link>
          <Link to="/weapon6">
            <li>-戦術-</li>
          </Link>
          <Link to="/weapon7">
            <li>-生存-</li>
          </Link>
        </ul>
      </div>
    </Box>
  );

  return (
    <div>
      {["絞り込み"].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button
            onClick={toggleDrawer(anchor, true)}
            disableElevation={true}
            sx={{
              backgroundColor: "#0A6347",
              color: "#fff",
              fontSize: "3rem",
              width: "50%",
              height: "80px",
              padding: "10px",
              fontFamily: "vdl-logog, sans-serif",
              fontWeight: 300,
              fontStyle: "normal",
              borderRadius: "0px",
              position: "fixed",
              bottom: 0,
              right: 0,
              zIndex: 0
            }}
          >
            {anchor}
          </Button>
          <Drawer
            anchor="right"
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            PaperProps={{
              sx: { width: "40%" }
            }}
          >
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}
