import "../styles.css";
import Drawer from "../drawer";

function Home() {
  return (
    <main id="top">
      <div class="page_top_sp">
        <div class="page_top_title_sp">
          <h2 class="title">高難易度でも超爽快！</h2>
          <p class="sub_title">~傑作アクションゲームの攻略データベース~</p>
        </div>
        <p class="top_sentence">
          軽快かつ爽快を感じさせるキャラクターの操作感に加え、多種多様でユニークな効果を持つ武器・アイテム、様々な特徴を持った多数の敵、そして、プレイするたびに世界が変化する周回要素。
          <br></br>
          Dead Cellsは、それら全てをもつ傑作アクションゲームです。
          <br></br>
          <br></br>
          しかし、このゲームは難易度が高く、初心者の方の中には骨を折られた方も多いと思います。そんな方たちの為に、最初の一周を攻略するためのデータをまとめました。
          <br></br>
          <br></br>
          このサイトにまとめたデータが、挫折したプレイヤーの助けになれば幸いです。
        </p>
        <Drawer />
      </div>
    </main>
  );
}

export default Home;
