import "../stylesPC.css";
import Drawer from "../drawerPC";

function Home() {
  return (
    <main id="top_pc">
      <div class="page_top_pc">
        <div class="page_top_title_pc">
          <h2 class="title_pc">高難易度でも超爽快！</h2>
          <p class="sub_title_pc">~傑作アクションゲームのデータベース~</p>
        </div>
        <p class="top_sentence_pc">
          軽快かつ爽快を感じさせるキャラクターの操作感に加え、多種多様でユニークな効果を持つ武器・アイテム、様々な特徴を持った多数の敵、そして、プレイするたびに世界が変化する周回要素。
          <br></br>
          Dead Cellsは、それら全てをもつ傑作アクションゲームです。
        </p>
        <Drawer />
      </div>
    </main>
  );
}

export default Home;
