import { Routes, Route, Link } from "react-router-dom";
import "./styles.css";
import "./drawer.css";
import Home from "./routes/home";
import Map from "./routes/map";
import Flow from "./routes/flow";
import Weapon from "./routes/weapon";
import Weapon2 from "./routes/weapon2";
import Weapon3 from "./routes/weapon3";
import Weapon4 from "./routes/weapon4";
import Weapon5 from "./routes/weapon5";
import Weapon6 from "./routes/weapon6";
import Weapon7 from "./routes/weapon7";

export default function App() {
  return (
    <div>
      <Link to="/">
        <div class="page_sp">
          <header class="page_title_sp"></header>
          <h1 class="page_title title">Dead Cellsのススメ！</h1>
        </div>
      </Link>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/flow" element={<Flow />} />
        <Route path="/map" element={<Map />} />
        <Route path="/weapon" element={<Weapon />} />
        <Route path="/weapon2" element={<Weapon2 />} />
        <Route path="/weapon3" element={<Weapon3 />} />
        <Route path="/weapon4" element={<Weapon4 />} />
        <Route path="/weapon5" element={<Weapon5 />} />
        <Route path="/weapon6" element={<Weapon6 />} />
        <Route path="/weapon7" element={<Weapon7 />} />
      </Routes>
    </div>
  );
}

