import "../flowPC.css";
import Drawer from "../drawer2PC";

function Flow() {
  return (
    <div>
      <Drawer />
      <main class="flow_pc">
        <h1 class="flow_title_pc">攻略の流れ</h1>
        <h2 class="flow_title_sub_pc">一周目をクリアするために</h2>
        <div class="flow_sentence_pc">
          <div class="flow_sentence_content_pc">
            <h2 class="flow_sentence_title_pc">-常に意識しておくこと-</h2>
            <p>
              まず初めに、このゲームを攻略にするにあたって必要なことは、周辺を常に警戒して動くということです。
              <br></br>
              <br></br>
              Dead
              Cellsのマップには、弱い敵から強力な敵まで多種多様な敵が無数に蔓延っています。
              <br></br>
              迂闊に進んでしまうとあっという間にそれらに囲まれてゲームオーバーに。
              <br></br>
              そうならないためにも意識してもらいたことがあります。
            </p>
          </div>
          <div class="flow_sentence_content_pc">
            <h2 class="flow_sentence_title_pc">
              -武器やアイテムの特徴を理解する-
            </h2>
            <p>
              このゲームは道中で拾う武器やアイテムを駆使して進めていくことが基本になります。
              <br></br>
              <br></br>
              武器やアイテムには独自の性能を持ち、特定の条件下で攻撃するとダメージが上昇したり、敵の身動きを封じたりするものなど、効果は様々です。
              <br></br>
              <br></br>
              どの武器がどんな性能を持っているかを理解し、それにあった立ち回りを行うことで攻略が捗ります。
              <br></br>
              プレイを重ねて、自分に合うものを見つけてみてください。
            </p>
          </div>
          <div class="flow_sentence_content_pc">
            <h2 class="flow_sentence_title_pc">-パリィや回避を駆使しよう-</h2>
            <p>
              攻撃だけでなく防御も大切です。
              <br></br>
              盾と回避は、このゲームの攻略においてとても強力な手助けになります。
              <br></br>
              <br></br>
              盾にはパリィといって、敵の攻撃に合わせて防御を行うと、それをノーダメージで弾く機能があります。
              <br></br>
              敵の種類や動作を知っておく必要がありますが、これさえ極めてしまえば、どんな敵に対しても有利な体勢で挑むことができるようになり、一周目の攻略を容易にします。
              <br></br>
              <br></br>
              回避も忘れてはいけません。
              <br></br>
              危険な状況に直面した場合は、とりあえず回避をしておけばその状況をすり抜けることができるかもしれません。
              <br></br>
              回避には長い無敵時間があり、その後の硬直時間もそれほどないため、敵の攻撃をすり抜けたい時や背後に回りたい時などに役に立ちます。
              <br></br>
              <br></br>
              最初のうちは回避で敵の攻撃を見極め、タイミングが理解できたらパリィを差し込むといった流れを掴めると良いです。
            </p>
          </div>
          <div class="flow_sentence_content_pc">
            <h2 class="flow_sentence_title_pc">-ステータス振りは極振りで-</h2>
            <p>
              道中では「スクロール」という、プレイヤーのステータスを強化するアイテムを拾うことがあります。
              <br></br>
              <br></br>
              拾った際に、赤と紫と緑の選択肢から強化したいものを選ぶことになるのですが、自分が使っていきたい武器と同じ色のものだけを選んでおけば大丈夫です。
              <br></br>
              同じ色の強化を積み重ねておけば、攻撃力が大きく上がり、また、スキルの効果も上昇します。
              <br></br>
              序盤はこれで大丈夫なのかと思ってしまうかもしれませんが、敵の量やHPが多くなる終盤では、その効果が目に見えてわかるようになります。
              <br></br>
              <br></br>
              スクロールによるステータス強化はとても重要な要素なので、漏れがないように拾っていきましょう。
            </p>
          </div>
        </div>
      </main>
    </div>
  );
}

export default Flow;
