import React from "react";
import fs from "fs";

export default function BasicCard() {
  const readFile = fs.readFileSync("src/map.json", "utf-8");
  const mapInfo = JSON.parse(readFile);

  return mapInfo.map((map, index) => {
    function scroll3() {
      const items = [];
      if (map.scroll_3 == 0) {
        items.push("なし");
      } else {
        items.push(map.scroll_3 + "個");
      }

      return items;
    }
    function scroll2() {
      const items = [];
      if (map.scroll_2 == 0) {
        items.push("なし");
      } else {
        items.push(map.scroll_2 + "個");
      }

      return items;
    }

    return (
      <div>
        <div class="map_content" key={index}>
          <img class="map_img" src={map.img} alt={map.name} />
          <h2 class="map_name">{map.name}</h2>
          <p>三択スクロールの数：{scroll3()}</p>
          <p>二択スクロールの数：{scroll2()}</p>
        </div>
      </div>
    );
  });
}
