import React from "react";
import fs from "fs";

export default function BasicCard() {
  const readFile = fs.readFileSync("src/weapon.json", "utf-8");
  const weaponInfo = JSON.parse(readFile);

  return weaponInfo.map((weapon, index) => {
    function type() {
      const items = [];
      if (weapon.kind == "暴虐") {
        items.push(<p class="weapon_kind red">暴虐</p>);
      } else if (weapon.kind == "戦術") {
        items.push(<p class="weapon_kind blue">戦術</p>);
      } else if (weapon.kind == "生存") {
        items.push(<p class="weapon_kind green">生存</p>);
      } else if (weapon.kind == "暴虐・戦術") {
        items.push(
          <p class="weapon_kind">
            <span class="red">暴虐</span>・<span class="blue">戦術</span>
          </p>
        );
      } else if (weapon.kind == "暴虐・生存") {
        items.push(
          <p class="weapon_kind">
            <span class="red">暴虐</span>・<span class="green">生存</span>
          </p>
        );
      } else if (weapon.kind == "戦術・生存") {
        items.push(
          <p class="weapon_kind">
            <span class="blue">戦術</span>・<span class="green">生存</span>
          </p>
        );
      }

      return items;
    }
    function sort() {
      const items = [];
      if (
        weapon.kind == "戦術" ||
        weapon.kind == "暴虐・戦術" ||
        weapon.kind == "戦術・生存"
      ) {
        items.push(
          <div class="weapon_content">
            <h2 class="weapon_name">{weapon.name}</h2>
            <img class="weapon_img" src={weapon.img} alt={weapon.name} />
            <p class="weapon_type">{weapon.type}</p>
            {type()}
          </div>
        );
      }

      return items;
    }
    return (
      <div class="weapon_content2" key={index}>
        {sort()}
      </div>
    );
  });
}
